
.post-wrap {
  height: 50px;
}
.page-wrap {
  width: 50vw;
  padding: 10px 0;
  margin: auto;
  list-style: none;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.117647), 0 1px 4px rgba(0, 0, 0, 0.117647);
}
.page-item {
  display: inline-block;
  width: 30px;
  height: 30px;
  line-height: 30px;
  margin: 0 10px;
  border-radius: 5px;
  box-sizing: border-box;
}
.page-item.active {
    color: #fff;
    background-color: #42b983;
}



